import React from "react";

interface IFBBoxProps {}

const FBBox: React.FC<IFBBoxProps> = (props) => {
  React.useEffect(() => {
    const fbScript: HTMLScriptElement = document.createElement("script");
    fbScript.id = "scrptFBLike";
    fbScript.async = true;
    fbScript.crossOrigin = "annonymous";
    fbScript.src =
      "https://connect.facebook.net/es_LA/sdk.js#xfbml=1&version=v6.0";
    window.document.body.appendChild(fbScript);
  }, []);

  return (
    <div
      className="fb-like"
      data-href="https://www.facebook.com/BrunoValentinaStore"
      data-width=""
      data-layout="button_count"
      data-action="like"
      data-size="small"
      data-share="false"
    ></div>
  );
};

export default FBBox;
